@import '../../globalVariable';

.twoFactorVerification {
  margin: 0 30px;
  .mainContent {
    margin-top: 30px;
  }

  .title {
    margin-bottom: 8px;
    font-size: 18px;
    font-weight: 300;
    text-align: center;
  }

  .sendTip {
    text-align: center;
    .sendTo {
      font-size: $bigFontSize;
    }
  }

  .inputWrapper {
    margin-top: 30px;
  }

  .linkWrapper {
    @include linkButton;
  }
  .confirmBtn {
    margin-top: 10px;
  }
}